<template>
  <div class="rx">
    <div class="visualizer">
      <app-visualizer transition="fade-in" v-if="started" :setup="{audioCtx: rx.audioCtx, srcNode: rx.procNode}"></app-visualizer>
    </div>
    <div class="spinner" v-show="!started && !error && !disconnect && !eot">
      <div class="bounce1"></div>
      <div class="bounce2"></div>
      <div class="bounce3"></div>
    </div>

    <div class="success" transition="fade-in" v-show="started">
      <span class="yay">Yay!</span>
      <p class="msg">You are connected :)</p>
    </div>

    <div class="error" transition="fade-in" v-show="error">
      <span class="dang">Dang!</span>
      <p class="msg" v-for="e in error">{{{ e }}}</p>
    </div>

    <div class="eot" transition="fade-in-msg" v-show="eot">
      <span class="bummer">Bummer!</span>
      <p class="msg">Transmission has ended</p>
    </div>
  </div>
</template>

<script>

  function state() {
    return {
      rx: null,
      started: false,
      error: null,
      eot: false
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {
      'app-visualizer': require('./visualizer.vue')
    },
    ready() {
      const channel = encodeURIComponent(window.location.href.substr(window.location.href.length - 16));
      try {
        this.rx = new facile.Rx(channel);
      } catch (e) {
        console.log(e);
        this.error = ['Something went wrong :('];
        return;
      }
      this.rx.start().then(() => {
        this.started = true;

        // ios kludge
        window.addEventListener('touchend', () => {
          var buffer = this.rx.audioCtx.createBuffer(1, 1, this.rx.audioCtx.sampleRate);
          var source = this.rx.audioCtx.createBufferSource();
          source.buffer = buffer;
          source.connect(this.rx.audioCtx.destination);
          source.noteOn(0);
        }, {once: true}, false);

      }).catch((e) => {
        console.log(e);
        if (e.message === 'unsupported') {
          this.error = ['Looks like your web browser is not supported yet :(', 'Take a look at the <a href="#faq">FAQ</a> section.'];
        } else if (e.message === 'invalid channel') {
          this.error = ['No active transmission found :('];
        } else {
          this.error = ['Something went wrong :('];
        }
      });
      this.rx.on('error', (e) => {
        console.log(e);
        this.started = false;
        this.rx.destroy();
        if (e === 'clients limit reached') {
          this.error = ['Active clients limit has been reached.', 'Please try again in a bit.'];
        } else {
          this.error = ['Something went wrong :('];
        }
      });
      this.rx.on('disconnect', () => {
        this.started = false;
        this.rx.destroy();
        this.error = ['No active transmission found :('];
      });
      this.rx.on('eot', () => {
        this.started = false;
        this.eot = true;
        this.rx.destroy();
      });
      window.onhashchange = () => {
        if (this.rx) {
          this.rx.destroy();
          this.started = false;
          this.rx = null;
        }
      }
    }
  };

  module.exports = vm;
</script>

<style scoped>
  .rx {
    font-family: 'Muli', sans-serif;
  }

  .visualizer {
    width: 320px;
    height: 100px;
  }
</style>



