<template>
  <div class="snippets">
    <div v-for="s in snippets" class="snippet">
      <span class="label">{{ s.label }}</span>
      <pre><code>{{{ s.code | highlight }}}</code></pre>
    </div>
  </div>
</template>

<script>

  function state() {
    return {
      snippets: require('../assets/snippets.js')
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {}
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .snippets {
    margin: 32px auto;
  }

  .snippet {
    display: inline-block;
    box-shadow: 0 0 2px rgba(0,0,0,.22),0 2px 2px rgba(0,0,0,.3);
    border-radius: 2px;
    width: 430px;
    padding: 8px;
    margin: 0 8px 0 8px;
    text-align: left;
    box-sizing: border-box;
    background: white;
    font-family: 'Muli', sans-serif;
  }

  @media all and (max-width: 872px) {
    .snippet {
      width: 480px;
      margin: 8px 0 8px 0;
    }
  }

  @media all and (max-width: 640px) {
    .snippet {
      width: 420px;
      margin: 8px 0 8px 0;
    }
  }

  @media all and (max-width: 480px) {
    .snippet {
      width: 280px;
      margin: 8px 0 8px 0;
    }
  }

  .snippet pre {
    margin: 0;
  }

  .snippet .label {
    display: block;
    margin-bottom: 8px;
    font-weight: 400;
  }

</style>



