module.exports = "<section><h1>0.1.0 API Reference</h1> <h2>facile</h2> <p>The <code>facile</code> object is the main application container. It is exposed as a global object.</p> <p>It's the only global handle provided and it's used for the creation of both transmitter and receiver objects.</p> <h2>Class:facile.Tx</h2> <p>This class is used to create a transmitter.</p> <p><code>facile.Tx</code> extends the <a href=https://nodejs.org/api/events.html#events_class_eventemitter target=_blank rel=\"noopener noreferrer\">EventEmitter</a> class.</p> <h3>new facile.Tx([options])</h3> <p>Creates a transmitter object.</p> <p><code>options</code> &lt;Object&gt; - An optional configuration object.</p> <ul> <li><code>server</code> &lt;String&gt; - The URL of the server to transmit to.</li> <li><code>quality</code> &lt;String&gt; - Audio encoding quality <ul> <li>'worst' - 32Kbps bitrate</li> <li>'low' - 64Kbps bitrate</li> <li>'medium' - 96Kbps bitrate</li> <li>'high' - 128Kbps bitrate</li> <li>'best' - 256Kbps bitrate</li> </ul> </li> </ul> <pre><code class=language-js><span class=builtin>var </span>tx = <span class=key>new</span> facile.Tx({\n  server: <span class=string>'http://localhost:8045'</span>,\n  quality: <span class=string>'high'</span>\n});\n</code></pre> <h3>Tx.start()</h3> <p>Starts the transmitter.</p> <p>Returns a <a href=https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Global_Objects/Promise target=_blank rel=\"noopener noreferrer\">Promise</a> which if fulfilled provides:</p> <ul> <li><code>channel</code> &lt;String&gt; - the channel unique identifier</li> </ul> <pre><code class=language-js><span class=builtin>var </span>tx = <span class=key>new</span> Tx();\n\ntx.start().then(<span class=builtin>function</span>(<span class=arg>channel</span>) {\n <span class=comment> // channel is your channel id</span>\n}).catch(<span class=builtin>function</span>(<span class=arg>e</span>) {\n <span class=comment> // handle error</span>\n});\n</code></pre> <h3>Tx.stop()</h3> <p>Stops the transmitter.</p> <h3>Tx.destroy()</h3> <p>Stops the transmitter if running and removes all events listeners.</p> <h3>Events</h3> <ul> <li><strong>error</strong> - fired in case of an error occured</li> <li><strong>connect</strong> - fired upon a successful connection to the server</li> <li><strong>disconnect</strong> - fired upon a disconnection from the server</li> </ul> <pre><code class=language-js>tx.on(<span class=string>'connect'</span>, <span class=builtin>function</span>(<span class=arg></span>) {\n  <span class=builtin>console</span>.log(<span class=string>'connected!'</span>);\n});\n\ntx.on(<span class=string>'error'</span>, <span class=builtin>function</span>(<span class=arg>e</span>) {\n  <span class=builtin>console</span>.log(<span class=string>'an error occured'</span>);\n});\n</code></pre> <h2>Class:facile.Rx</h2> <p>This class is used to create a receiver.</p> <p><code>facile.Rx</code> extends the <a href=https://nodejs.org/api/events.html#events_class_eventemitter target=_blank rel=\"noopener noreferrer\">EventEmitter</a> class.</p> <h3>new facile.Rx(channel, [options])</h3> <p>Creates a receiver object.</p> <p><code>channel</code> &lt;String&gt; - the identifier of the channel to receive from.</p> <p><code>options</code> &lt;Object&gt; - An optional configuration object.</p> <ul> <li><code>server</code> &lt;String&gt; - The URL of the server to transmit to. It can include a port number.</li> </ul> <pre><code class=language-js><span class=builtin>var </span>rx = <span class=key>new</span> facile.Rx(<span class=string>'a4b621a570fd2c06'</span>);\n</code></pre> <h3>Rx.start()</h3> <p>Starts the receiver.</p> <p>Returns a <a href=https://developer.mozilla.org/en/docs/Web/JavaScript/Reference/Global_Objects/Promise target=_blank rel=\"noopener noreferrer\">Promise</a>.</p> <pre><code class=language-js><span class=builtin>var </span>rx = <span class=key>new</span> Rx();\n\nrx.start().then(<span class=builtin>function</span>(<span class=arg></span>) {\n <span class=comment> // receiver started</span>\n}).catch(<span class=builtin>function</span>(<span class=arg>e</span>) {\n <span class=comment> // handle error</span>\n});\n</code></pre> <h3>Rx.stop()</h3> <p>Stops the receiver.</p> <h3>Rx.destroy()</h3> <p>Stops the receiver if running and removes all events listeners.</p> <h3>Events</h3> <ul> <li><strong>error</strong> - fired in case of an error occured</li> <li><strong>connect</strong> - fired upon a successful connection to the server</li> <li><strong>disconnect</strong> - fired upon a disconnection from the server</li> <li><strong>eot</strong> - fired when the transmission ends</li> </ul> <pre><code class=language-js>rx.on(<span class=string>'eot'</span>, <span class=builtin>function</span>(<span class=arg></span>) {\n  <span class=builtin>console</span>.log(<span class=string>'transmission ended!'</span>);\n});\n</code></pre> </section>";


