<template>
  <div class="header">
    <div class="wrapper">
      <img v-show="show" transition="fade-in-logo" class="logo" src="../assets/facile-logo.png" alt="logo" />
      <div class="text">
        <h1 v-show="show" transition="fade-in" class="brand">Facile.Audio</h1>
        <p v-show="show" transition="fade-in-sub" class="sub">Stupidly Simple Audio Streaming</p>
      </div>
    </div>
  </div>
</template>

<script>

  function state() {
    return {
      show: false
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {},
    ready() {
      this.show = true;
    }
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .header {
    width: 100%;
    text-align: center;
    background: #303040;
    color: #9CADBF;
  }

  .brand {
    font-family: 'Nunito', sans-serif;
    font-size: 4em;
    margin: 0;
    margin-left: -0.06em;
  }

  .sub {
    font-weight: 400;
    margin-left: 0.2em;
    margin: 0;
    font-family: 'Muli', sans-serif;
  }

  span {
    display: block;
  }

  .text {
    text-align: left;
    margin: 0 auto;
    display: inline-block;
  }

  .logo {
    display: inline-block;
    width: 6em;
    -ms-transform: translate(-0.3em, 0.3em);
    -webkit-transform: translate(-0.3em, 0.3em);
    transform: translate(-0.3em, 0.3em);
  }

  .wrapper {
    padding: 4em;
    padding-bottom: 1em;
  }

</style>



