const tx = {label: 'Transmit', code: `var tx = new facile.Tx();
tx.start().then(function(channel) {
  console.log('transmitter started');
}).catch(function(e) {
  console.log(e);
});`};

const rx = {label: 'Receive', code: `var rx = new facile.Rx(channel);
rx.start().then(function() {
  console.log('receiver started');
}).catch(function(e) {
  console.log(e);
});`};

const snippets = [tx, rx];

module.exports = snippets;



