<template>
  <div>
    <app-menu></app-menu>
    <app-header></app-header>
    <div class="main" v-show="show" transition="main">
      <router-view transition="fade" transition-mode="out-in"></router-view>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>

  function state() {
    return {
      show: false
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {
      'app-header': require('./components/header.vue'),
      'app-footer': require('./components/footer.vue'),
      'app-menu': require('./components/menu.vue')
    },
    ready() {
      this.show = true;
    }
  };

  module.exports = vm;
</script>

<style lang="stylus">

  body {
    margin: 0;
    background: #f9f9f9;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    box-sizing: border-box;
    font-size: 14px;
    text-align: center;
  }

  .main {
    height: calc(100vh - 200px);
    min-height: 200px;
    max-width: 1200px;
    min-width: 320px;
    display: inline-block;
    text-align: center;
  }

  @media all and (max-width: 640px) {
    body {
      font-size: 14px;
    }
  }

  @media all and (max-width: 480px) {
    body {
      font-size: 12px;
    }
  }

</style>



