<template>
  <div class="tx">
    <div class="visualizer">
      <app-visualizer transition="fade-in" v-if="started" :setup="{audioCtx: tx.audioCtx, srcNode: tx.srcNode}"></app-visualizer>
    </div>
    <div class="spinner" v-show="!started && !error">
      <div class="bounce1"></div>
      <div class="bounce2"></div>
      <div class="bounce3"></div>
    </div>

    <div class="success" transition="fade-in" v-show="url">
      <span class="yay">Yay!</span>
      <p class="msg">You are live :)</p>
    </div>

    <div class="error" transition="fade-in" v-show="error">
      <span class="dang">Dang!</span>
      <p class="msg" v-for="e in error">{{{ e }}}</p>
    </div>

    <a :href="url" target="_blank" rel="noopener noreferrer" v-show="url" class="url" transition="fade-in">{{ url }}</a>
  </div>
</template>

<script>

  function state() {
    return {
      tx: null,
      url: null,
      error: null,
      started: false
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {
      transmit() {
        try {
          this.tx = new facile.Tx();
        } catch (e) {
          console.log(e);
          this.error = ['Something went wrong :('];
          return;
        }
        this.tx.start().then((channel) => {
          this.url = window.location.protocol + '//' + window.location.hostname;
          if (window.location.port) {
            this.url += ':' + window.location.port;
          }
          this.url += '/#!/rx?' + channel;
          console.log('transmitter started ' + channel);
          this.started = true;
        }).catch((e) => {
          if (e.message === 'unsupported') {
            this.error = ['Looks like your web browser is not supported :(', 'You can check the <a href="#faq">FAQ</a> section.'];
          } else {
            this.error = ['Something went wrong :('];
          }
        });
        this.tx.on('error', (e) => {
          console.log(e);
          if (e === 'channels limit reached') {
            this.error = ['Active channels limit has been reached.', 'Please try again in a bit.'];
          } else {
            this.error = ['Something went wrong :('];
          }
        });
      }
    },
    components: {
      'app-visualizer': require('./visualizer.vue')
    },
    ready() {
      this.transmit();
      window.onhashchange = () => {
        if (this.tx) {
          this.tx.destroy();
          this.started = false;
          this.tx = null;
        }
      }
    }
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .visualizer {
    width: 320px;
    height: 100px;
  }

  .tx {
    font-family: 'Muli', sans-serif;
  }

  .url {
    font-size: 1em;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
  }

</style>



