<template>
  <div class="credits">
    <p>This is a noncomprehensive, randomly ordered list of due credits:</p>
    <div class="credit" v-for="credit in credits">
      <a :href="credit.url"><span class="name">{{ credit.name }}</span></a>
      <p v-if="credit.text">{{ credit.text }}</p>
    </div>
  </div>
</template>

<script>

  function shuffle(a) {
    let j;
    let x
    for (let i = a.length; i; --i) {
      j = Math.floor(Math.random() * i);
      x = a[i - 1];
      a[i - 1] = a[j];
      a[j] = x;
    }
    return a;
  }

  function state() {
    return {
      credits: shuffle(require('../assets/credits.json'))
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {}
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .credits {
    box-sizing: border-box;
    margin: 16px auto;
    padding: 1em;
    text-align: center;
    box-shadow: 0 0 2px rgba(0,0,0,.12),0 2px 2px rgba(0,0,0,.2);
    border-radius: 2px;
    background: white;
    max-width: 720px;
  }

  .credit {
    padding: 0.2em;
  }

  span.name {
    font-size: 1.2em;
  }

  .credit a {
    cursor: pointer;
    text-decoration: none;
    color: black;
    transition: all 100ms cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
  }

  .credit a:hover {
    color: #0064FF;
  }

</style>



