import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'

Vue.use(Router);

require('./assets/common.css');
require('./assets/loader.css');
require('./assets/transitions.css');

Vue.filter('highlight', (value) => {
  let re;
  let code = value;
  re = new RegExp('function\\((.*)\\)', 'g');
  code = code.replace(re, 'function(<span class="arg">$1</span>)');
  re = new RegExp("'([^\"']+)'", 'g');
  code = code.replace(re, '<span class="string">\'$1\'</span>');
  re = new RegExp('(\/\/.*)', 'g');
  code = code.replace(re, '<span class="comment">$1</span>');
  code = code.replace(new RegExp('var ', 'g'),
   '<span class="builtin">var </span>');
  code = code.replace(new RegExp('return ', 'g'),
   '<span class="key">return </span>');
  code = code.replace(new RegExp('function', 'g'),
   '<span class="builtin">function</span>');
  code = code.replace(new RegExp('new', 'g'),
   '<span class="key">new</span>');
  code = code.replace(new RegExp('console', 'g'),
   '<span class="builtin">console</span>');
  return code;
});

const app = Vue.extend({
  el: function() {
    return 'body';
  },
  components: {
    App
  }
});

const router = new Router();

router.map({
  '': {
    component: require('./components/main.vue')
  },
  '/tx': {
    component: require('./components/tx.vue')
  },
  '/rx': {
    component: require('./components/rx.vue')
  },
  '/faq': {
    component: require('./components/faq.vue')
  },
  '/credits': {
    component: require('./components/credits.vue')
  },
  '/support': {
    component: require('./components/support.vue')
  },
  '/api': {
    component: require('./components/api.vue')
  }
});

router.beforeEach((transition) => {
  window.scrollTo(0, 0)
  transition.next()
});

router.start(app, 'body');



