<template>
  <div class="visual">
    <canvas id="visualizer" width="320" height="100"></canvas>
  </div>
</template>

<script>

  function state() {
    return {};
  }

  const vm = {
    data() {
      return state();
    },
    props: {
      setup: {
        type: Object,
        required: true
      }
    },
    methods: {},
    components: {},
    ready() {

      var analyserContext = null;
      var analyserNode = null;
      var canvasWidth;
      var canvasHeight;

      analyserNode = this.setup.audioCtx.createAnalyser();
      analyserNode.fftSize = 1024;

      analyserNode.minDecibels = -120;
      analyserNode.maxDecibels = 120;
      analyserNode.smoothingTimeConstant = 0.80;

      this.setup.srcNode.connect(analyserNode);

      const updateAnalysers = function (time) {
        if (!analyserContext) {
          var canvas = document.getElementById('visualizer');
          canvasWidth = canvas.width;
          canvasHeight = canvas.height;
          analyserContext = canvas.getContext('2d');
        }
        var SPACING = 8;
        var BAR_WIDTH = 6;
        var numBars = Math.round(canvasWidth / SPACING);
        var freqByteData = new Uint8Array(analyserNode.frequencyBinCount);
        analyserNode.getByteFrequencyData(freqByteData);
        analyserContext.clearRect(0, 0, canvasWidth, canvasHeight);
        //analyserContext.fillStyle = '#F6D565';
        //analyserContext.lineCap = 'round';
        var multiplier = analyserNode.frequencyBinCount / numBars;
        for (var i = 0; i < numBars; ++i) {
          var magnitude = 0;
          var offset = Math.floor( i * multiplier );
          for (var j = 0; j< multiplier; j++) {
            magnitude += freqByteData[offset + j];
          }
          magnitude = (magnitude / multiplier);
          var magnitude2 = freqByteData[i * multiplier];
          analyserContext.fillStyle = "hsl( " + Math.round(190 + (i*100)/numBars) + ", 75%, 60%)";
          analyserContext.fillRect(i * SPACING, 0, BAR_WIDTH, magnitude);
        }
        window.requestAnimationFrame(updateAnalysers);
      }


      updateAnalysers();

    }
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  #visualizer {
    display: inline-block;
    transition: all 150ms cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
    //opacity: 0.5;
  }

</style>



