<template>
  <div>
    <app-features></app-features>
    <span class="showMe" @click="onShowMe" transition="fade-in-sub">Show Me</span>
    <pre class="lib" @click="selectContent"><code>&lt;<span class="key">script</span> src=<span style="color: #0064FF;">"https://facile.audio/dist/facile-audio.js"</span>&gt;&lt;/<span class="key">script</span>&gt;</code></pre>
    <app-snippets></app-snippets>
    <p class="support">Like what you see? If so, consider <a href="#support"><strong>becoming a supporter</strong></a> :)</p>
  </div>
</template>

<script>

  function selectElementContents(el) {
    var range = document.createRange();
    range.selectNodeContents(el);
    var sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
  }

  function state() {
    return {
      hideButton: false
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {
      onShowMe(ev) {
        this.$router.go('/tx');
      },
      selectContent() {
        selectElementContents(document.querySelector('pre.lib'));
      }
    },
    components: {
      'app-features': require('./features.vue'),
      'app-snippets': require('./snippets.vue')
    }
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .lib {
    display: block;
    width: 876px;
    font-size: 1.2em;
    text-align: center;
    outline: none;
    border: 0;
    box-shadow: 0 0 2px rgba(0,0,0,.22),0 2px 2px rgba(0,0,0,.3);
    border-radius: 2px;
    margin: 32px auto;
    box-sizing: border-box;
    background: white;
    overflow: auto;
  }

  .lib code {
    background: white;
    color: #7f8c8d;
  }

  @media all and (max-width: 872px) {
    .lib {
      width: 480px;
      font-size: 1em;
    }
  }

  @media all and (max-width: 640px) {
    .lib {
      width: 420px;
      font-size: 0.8em;
    }
  }

  @media all and (max-width: 480px) {
    .lib {
      width: 280px;
      font-size: 0.7em;
    }
  }

  .showMe {
    display: block;
    max-width: 120px;
    padding: 0.5em 0 0.5em 0;
    margin: 32px auto;
    background: #0096FF;
    color: white;
    font-weight: 400;
    cursor: pointer;
    transition: all 150ms cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
    box-shadow: 0 0 2px rgba(0,0,0,.22),0 2px 2px rgba(0,0,0,.3);
    font-family: 'Muli', sans-serif;
    font-size: 1em;
  }

  .showMe:hover {
    background: #0064FF;
    -webkit-transform: scale(1.03);
    transform: scale(1.03);
  }

  .support {
    margin-top: 0;
    font-family: 'Muli', sans-serif;
  }

  .support a {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }

</style>



