<template>
  <div class="features">
    <div v-for="f in features" class="feature">
      <span class="name">{{ f.name }}</span>
      <p v-for="d in f.desc" class="desc">{{ d }}</p>
    </div>
  </div>
</template>

<script>

  function state() {
    return {
      features: require('../assets/features.json')
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {}
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

  .features {
    margin: 32px auto;
  }

  .feature {
    display: inline-block;
    background: white;
    box-shadow: 0 0 2px rgba(0,0,0,.22),0 2px 2px rgba(0,0,0,.3);
    border-radius: 2px;
    width: 280px;
    padding: 8px;
    margin: 0 8px 0 8px;
    box-sizing: border-box;
    font-family: 'Muli', sans-serif;
    font-weight: 300;
  }

  @media all and (max-width: 872px) {
    .feature {
      width: 480px;
      margin: 8px 0 8px 0;
    }
  }

  @media all and (max-width: 640px) {
    .feature {
      width: 420px;
      margin: 8px 0 8px 0;
    }
  }

  @media all and (max-width: 480px) {
    .feature {
      width: 280px;
      margin: 8px 0 8px 0;
    }
  }

  span.name {
    display: block;
    font-weight: 400;
    text-align: left;
    margin-bottom: 8px;
  }

  p.desc {
    display: block;
    padding: 0 0 0 8px;
    margin: 0;
    color: #7f8c8d;
    text-align: left;
  }

</style>



