<template>
  <div class="faqs">
    <div v-for="faq in faqs" class="faq">
      <h3>{{ faq.heading }}</h3>
      <div class="wrapper">
        <p v-for="line in faq.lines">{{{ line }}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  function state() {
    return {
      faqs: require('../assets/faq.json')
    };
  }

  const vm = {
    data() {
      return state();
    },
    props: {},
    methods: {},
    components: {}
  };

  module.exports = vm;
</script>

<style lang="stylus" scoped>

.faq {
  margin: 16px auto;
  max-width: 720px;
  box-shadow: 0 0 2px rgba(0,0,0,.12),0 2px 2px rgba(0,0,0,.2);
  border-radius: 2px;
  box-sizing: border-box;
  //font-size: 0.8em;
  text-align: left;
  background: white;
  padding: 4px 8px 4px 8px;
}

.faq h3 {
  font-size: 1em;
  font-weight: 700;
  padding: 0;
  margin: 0;
}

.faq p {
  margin: 4px 0px 4px 4px;
}

</style>



